// hamburger menu


function hamburgerMenu (hamburgerSelector, hamMenuSelector, parentHamMenuSelector, menuCloseSelector, overlaySelector, fixedMenuSelector) {
    const hamburger = document.querySelector(hamburgerSelector);
    const hamMenu = document.querySelectorAll(hamMenuSelector);
    const parentHamMenu = document.querySelector(parentHamMenuSelector);
    const menuClose = document.querySelector(menuCloseSelector);
    const overlay = document.querySelector(overlaySelector);
    const fixedMenu = document.getElementById(fixedMenuSelector);


    function closeMenu() {
        parentHamMenu.style.display = 'none';
        overlay.style.display = 'none';
        fixedMenu.style.zIndex = '50';
        mobileMenuOpen = false;
    }

    function openMenu() {
        parentHamMenu.style.display = 'block';
        overlay.style.display = 'block';
        fixedMenu.style.zIndex = '0';
        mobileMenuOpen = true;

    }


    hamburger.addEventListener('click', openMenu);
    menuClose.addEventListener('click', closeMenu);

    parentHamMenu.addEventListener('click', (e) => {
        const target = e.target;

        if(target && target.classList.contains(hamMenuSelector)) {
            hamMenu.forEach((item) => {
                if(target === item) {
                    closeMenu();
                }
            });
        }
    });
/*     parentHamMenu.addEventListener('click', (e) => {
        const target = e.target;
      
        if (target && target.classList.contains('promo__hamItem')) {
          closeMenu();
        }
      }); */
      
}

export default hamburgerMenu;